import styled from 'styled-components';
import TrainingsButton from 'components/atoms/TrainingsButton/TrainingsButton';

export const TrainingsWrapper = styled.div`
  height: 100vh;
  background-color: ${({ theme }) => theme.colors.mainColor};
  display: flex;
  flex-direction: column;
  align-items: center;
  visibility: hidden;
`;

export const AddNewTraining = styled(TrainingsButton)`
  border: 1px solid ${({theme}) => theme.colors.lightGray};
  box-shadow: none;
  background-color: transparent;
  margin-top: 50px;
`;

export const BackButton = styled(TrainingsButton)`
  position: absolute;
  bottom: 10px;
`;