import styled from 'styled-components';

export const BackButton = styled.div`
  width: 30px;
  height: 30px;
  border-bottom: 2px solid ${({ theme }) => theme.colors.lightGrey};
  border-left: 2px solid ${({ theme }) => theme.colors.lightGrey};
  position: absolute;
  top: 20px;
  left: 25px;
  transform: rotate(45deg);
`;