import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { useAuth } from 'store/AuthContext';
import {
  HomeWrapper,
  StyledLink,
  IconButton,
  ImageWrapper,
  Logout
} from './Home.style';
import Button from 'components/atoms/Button/Button';
import { TRAININGS, WEIGHT } from 'constants/routes';
import gsap from 'gsap';
import { ReactComponent as TrainingIcon } from 'assets/icons/trainingIcon.svg';
import { ReactComponent as WeightIcon } from 'assets/icons/mainWeightIcon.svg';
import { ReactComponent as OptionsIcon } from 'assets/icons/optionsIcon.svg';

const Home = (props) => {
  const { logout } = useAuth();
  const _isMounted = useRef(true);
  let page = useRef(null);

  const handleLogout = async () => {
    try {
      if (_isMounted.current) {
        await logout();
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    const tl = gsap.timeline();
    tl.fromTo(page, { duration: 0.3, autoAlpha: 0 }, { autoAlpha: 1 });
    return () => (_isMounted.current = false);
  }, []);

  return (
    <HomeWrapper ref={(el) => (page = el)}>
      <StyledLink to={TRAININGS}>
        <IconButton color="#B45875">
          <ImageWrapper>
            <TrainingIcon />
          </ImageWrapper>
          Treningi
        </IconButton>
      </StyledLink>

      <StyledLink to={WEIGHT}>
        <IconButton color="#6E416C"><ImageWrapper>
            <WeightIcon />
          </ImageWrapper>Waga</IconButton>
      </StyledLink>

      <StyledLink to={TRAININGS}>
        <IconButton color="#EA7F6C"><ImageWrapper>
            <OptionsIcon />
          </ImageWrapper>Ustawienia</IconButton>
      </StyledLink>

      <StyledLink to={TRAININGS}>
        <Logout onClick={handleLogout}>
          Wyloguj si
        </Logout>
      </StyledLink>
    </HomeWrapper>
  );
};

Home.propTypes = {};

export default Home;
