import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {
  PageWrapper,
  ImageWrapper,
  NoWeight,
  Info,
  Modal,
  StyledInput,
  ButtonsWrapper,
  WeightNumber,
  UpdateWeightWrapper,
} from './Weight.style';
import { ReactComponent as ScaleIcon } from 'assets/icons/scaleIcon.svg';
import Label from 'components/atoms/Label/Label';
import Button from 'components/atoms/Button/Button';
import { useDatabase } from 'store/DatabaseContext';
import { useAuth } from 'store/AuthContext';
import Back from 'components/atoms/Back/Back';
import Diagram from 'components/molecules/Diagram/Diagram';

const Weight = (props) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [value, setValue] = useState('');
  const [userData, setUserData] = useState([]);
  const [weightData, setWeightData] = useState([]);

  const handleOnChange = (e) => setValue(e.target.value);

  const openModal = () => setIsModalOpen(true);
  const closeModal = () => setIsModalOpen(false);

  const { currentUser } = useAuth();
  const { updateWeight, addNewWeight, getWeightData, getActualWeight } = useDatabase();

  const setActualWeight = async () => {
    try {
      await updateWeight(currentUser.uid, value);
      await addNewWeight(currentUser.uid, value);
    } catch (error) {
      console.log(error);
    }
    closeModal();
    setValue('');
  };

  useEffect(async () => {
    try {
      await getWeightData(currentUser.uid, setWeightData);
      await getActualWeight(currentUser.uid, setUserData);
    } catch (error) {
      console.log(error);
    }
  }, []);
  console.log(userData, weightData);
  return (
    <PageWrapper>
      <Back />
      <ImageWrapper>
        <ScaleIcon />
      </ImageWrapper>
      {weightData ? (
        <>
          <WeightNumber>{`${userData && userData} kg`}</WeightNumber>
          <UpdateWeightWrapper>
            <StyledInput
              type="number"
              value={value}
              onChange={handleOnChange}
            />
            <Button disabled={value ? false : true} small onClick={setActualWeight}>ZMIE</Button>
          </UpdateWeightWrapper>
          <Diagram scaleData={true} data={weightData}/>
        </>
      ) : (
        <NoWeight>
          <Info>Nie ledzisz jeszcze informacji o Twojej wadze</Info>
          <Info underline onClick={openModal}>
            Zacznij!
          </Info>
        </NoWeight>
      )}

      {isModalOpen && (
        <Modal>
          <Info>Zapisz swoj wag startow</Info>
          <Label row>
            <StyledInput
              type="number"
              value={value}
              onChange={handleOnChange}
            />
            kg
          </Label>
          <ButtonsWrapper>
            <Button small onClick={closeModal}>
              WR
            </Button>
            <Button small active onClick={setActualWeight}>
              ZAPISZ
            </Button>
          </ButtonsWrapper>
        </Modal>
      )}
    </PageWrapper>
  );
};

Weight.propTypes = {};

export default Weight;
